/* src/app/components/orders/orders.scss */
.orders-container {
  display: flex;
  flex-direction: column;
  gap: 12px;
  padding: 0;
  flex: 1 1 auto;
  overflow-y: auto;
  padding-bottom: calc(var(--footer-height) + 8px);
  scroll-padding-bottom: calc(var(--footer-height) + 8px);
  box-sizing: border-box;
}
.orders-top-bar {
  display: flex;
  align-items: center;
  gap: 4px;
  height: 70px;
  position: sticky;
  top: 0;
  z-index: 20;
  -webkit-backdrop-filter: blur(4px);
  backdrop-filter: blur(4px);
  border-bottom: 1px solid var(--sys-outline, #222);
  background: var(--sys-surface-container-high, #181a20);
  box-shadow: 0 4px 14px rgba(0, 0, 0, 0.35);
}
.orders-controls {
  display: flex;
  align-items: center;
  gap: 18px;
  margin-left: 8px;
  flex: 1 1 auto;
}
.orders-actions-group {
  display: flex;
  align-items: center;
  gap: 8px;
}
.toolbar-btn.mat-mdc-outlined-button,
.toolbar-btn.mat-mdc-stroked-button {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  height: 34px;
  line-height: 34px;
  padding: 0 12px !important;
  font-size: 13px;
  font-weight: 500;
  border-color: #2a2d33 !important;
  background: rgba(255, 255, 255, 0.03);
  color: var(--sys-on-surface, #eee) !important;
  transition:
    background 0.2s ease,
    transform 0.2s ease,
    box-shadow 0.2s ease;
}
.toolbar-btn.mat-mdc-outlined-button mat-icon,
.toolbar-btn.mat-mdc-stroked-button mat-icon {
  font-size: 18px;
  width: 18px;
  height: 18px;
}
.toolbar-btn.mat-mdc-outlined-button:hover:not([disabled]),
.toolbar-btn.mat-mdc-stroked-button:hover:not([disabled]) {
  background: rgba(255, 255, 255, 0.08);
  transform: translateY(-1px);
}
.toolbar-btn.mat-mdc-outlined-button:active:not([disabled]),
.toolbar-btn.mat-mdc-stroked-button:active:not([disabled]) {
  transform: translateY(0);
  background: rgba(255, 255, 255, 0.12);
}
.toolbar-btn.mat-mdc-outlined-button[disabled],
.toolbar-btn.mat-mdc-stroked-button[disabled] {
  opacity: 0.5;
}
.dense-select.mat-mdc-form-field {
  max-width: 150px;
  height: 34px;
  margin-top: -22px;
  margin-left: auto;
}
.dense-select.mat-mdc-form-field .mat-mdc-form-field-flex {
  min-height: 34px;
  height: 34px;
  padding: 0 8px !important;
}
.dense-select.mat-mdc-form-field .mat-mdc-form-field-infix {
  padding: 0 !important;
  min-height: 34px !important;
}
.dense-select.mat-mdc-form-field .mat-mdc-select-trigger {
  height: 34px;
  display: flex;
  align-items: center;
}
.dense-select.mat-mdc-form-field .mat-mdc-select-value,
.dense-select.mat-mdc-form-field .mat-mdc-select-placeholder {
  font-size: 13px;
}
.dense-select.mat-mdc-form-field .mat-mdc-form-field-outline-start,
.dense-select.mat-mdc-form-field .mat-mdc-form-field-outline-end {
  height: 34px;
}
.orders-accordion .mat-expansion-panel {
  background: var(--sys-surface-container, #20242b);
  border: 1px solid #262a30;
  border-radius: 10px !important;
  margin-bottom: 8px;
  overflow: hidden;
}
.orders-accordion .mat-expansion-panel .mat-expansion-panel-header {
  padding: 0 14px;
  min-height: 46px;
  background:
    linear-gradient(
      90deg,
      rgba(255, 255, 255, 0.04),
      rgba(255, 255, 255, 0));
}
.orders-accordion .mat-expansion-panel .mat-expansion-panel-header:hover {
  background:
    linear-gradient(
      90deg,
      rgba(255, 255, 255, 0.08),
      rgba(255, 255, 255, 0.02));
}
.orders-accordion .mat-expansion-indicator::after {
  color: var(--sys-on-surface, #bbb);
}
.order-card {
  border-radius: 16px;
}
.order-row {
  display: flex;
  justify-content: space-between;
  margin: 4px 0;
}
.order-row span:first-child {
  font-weight: 500;
  color: #666;
}
.order-row span:last-child {
  font-weight: 600;
}
.created-at {
  font-size: 0.8rem;
  color: #999;
}
.spacer {
  flex: 1 1 auto;
}
.spinner-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem 0;
}
.summary-card {
  margin-bottom: 1rem;
  background: var(--sys-surface-container-high, #181a20);
  color: var(--sys-on-surface, #ddd);
  border: 1px solid #262a30;
  border-radius: 14px;
  box-shadow: 0 3px 12px rgba(0, 0, 0, 0.4);
}
.summary-card .summary-row {
  display: flex;
  justify-content: space-between;
  padding: 0.3rem 0;
  font-size: 0.95rem;
}
.summary-card .summary-row strong.positive {
  color: #4caf50;
}
.summary-card .summary-row strong.negative {
  color: #f44336;
}
.order-row {
  display: flex;
  justify-content: space-between;
  padding: 0.25rem 0;
  font-size: 0.84rem;
  color: var(--sys-on-surface, #ccc);
}
.order-row span:first-child {
  color: #8a8f99;
}
.order-row .positive {
  color: #4caf50;
  font-weight: 600;
}
.order-row .negative {
  color: #f44336;
  font-weight: 600;
}
.order-card.clickable {
  cursor: pointer;
  transition: transform 0.1s ease, box-shadow 0.1s ease;
}
.order-card.clickable:hover {
  transform: translateY(-2px);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
}
.positive {
  color: #28a745;
  font-weight: 600;
}
.negative {
  color: #dc3545;
  font-weight: 600;
}
.mat-mdc-outlined-button.grey {
  border-color: #9e9e9e !important;
  color: #616161 !important;
}
.mat-mdc-outlined-button.green {
  border-color: #4caf50 !important;
  color: #2e7d32 !important;
}
.mat-mdc-outlined-button.red {
  border-color: #f44336 !important;
  color: #b71c1c !important;
}
.watchlist-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}
